.norecord {
    display: flex;
    justify-content: center;
    padding: 10px;
    border: 1px solid #ccc9;
    border-top: transparent;
    background-color: #ffffff !important;
    border-bottom-right-radius: 10px !important;
    border-bottom-left-radius: 10px !important;
    margin: 0 12px;
    width: inherit !important;
}

.page-container {
    background: #fff;
    min-height: 500px;
    border-radius: 14px;
    margin: 20px 0px 14px 0px;
}

.page-container p.filter-status-text {
    padding: 14px 14px 0px;
}

#tbl-container {
    display: flex;
    flex-direction: column;
    min-width: 300px;
    background: #fff;
}

.mat-table {
    overflow: auto;
    border-radius: 10px;
    padding: 0 12px;
}

.mat-header-row {
    background: #ed8c01;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.mobile-label {
    display: none;
}

.data-not-found {
    text-align: center;
    margin: 100px;
    font-weight: bold;
}

@media(max-width: 767px) {
    .mobile-label {
        width: 80px;
        display: inline-block;
        font-weight: bold;
    }

    .mat-header-row {
        display: none;
    }

    .mat-row {
        flex-direction: column;
        align-items: start;
        padding: 8px 24px;
    }
}

.mattext {
    padding-top: 15px;
}

.mattext .mat-header-cell {
    color: #fff;
    font-size: 13px;
    font-weight: 600;
}

mat-row.mat-row:hover {
    background: #ed8c0159;
    cursor: pointer;
}

@media screen and (max-width: 767px) {

    .mat-cell,
    .mat-footer-cell {
        width: 100%;
        min-height: 25px;
        display: flex;
        align-items: flex-start;
    }

    .mobile-label {
        width: 55%;
    }

    .mobile-text {
        width: 45%;
    }

    mat-cell:first-of-type,
    mat-footer-cell:first-of-type,
    mat-header-cell:first-of-type {
        padding-left: 0;
    }

    #tbl-container,
    .page-container,
    .mat-table {
        background: none;
        box-shadow: none;
    }

    mat-footer-row,
    mat-row {
        min-height: auto;
    }

    .mat-row {
        border-radius: 10px;
        margin-bottom: 15px;
    }

    mat-cell:last-of-type,
    mat-footer-cell:last-of-type,
    mat-header-cell:last-of-type {
        padding-right: 0;
    }

    .search-res {
        padding: 20px 0 0 0 !important;
    }

    .p-id,
    .p-name {
        font-weight: bold;
    }

    .title-container {
        margin: 0 0px 20px;
        text-align: center;
        background: #fff;
        padding: 15px;
        border-top: 2px solid #d2cdcd;
        border-bottom: 2px solid #d2cdcd;
    }

    .page-container p.filter-status-text {
        padding: 0px 14px 0px;
    }

    .data-not-found {
        margin: inherit;
    }

    .data-not-found p {
        background: #fff;
        padding: 12px;
    }

    .page-title {
        margin-left: -12%;
    }

    .page-container {
        min-height: 100% !important;
    }

    #tbl-container {
        height: 100% !important;
    }

    .wrapper {
        display: inherit;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .mattext .mat-header-cell:nth-child(2),
    .mattext .mat-header-cell:nth-child(1),
    .mattext .mat-header-cell:nth-child(3) {
        min-width: 110px;
    }

    .mattext .mat-header-cell:last-child {
        min-width: 200px;
    }
}

@media screen and (min-width: 1025px) and (max-width: 1180px) {
    .mattext .mat-header-cell:last-child {
        min-width: 200px;
    }
}